<template>
  <div class="hotmuisc">
    <div class="hotpic">
      <span class="hotpic-title"></span>
      <span class="hot-time">更新日期:05月06日</span>
    </div>   
    <music-list v-if="showList.length>0" :musiclist="showList" @play="$emit('play',$event)" :shownum="shownum"></music-list>
    <load-animate v-else></load-animate>
    <div class="loadmore" ref='loadmore'>{{page>=10?"没有更多啦(●'◡'●)":'正在努力赶来ing...'}}</div>
  </div>
</template>

<script>
import MusicList from '@/components/MusicList.vue'
import LoadAnimate from '@/components/LoadAnimate.vue'

export default {
components:{
  MusicList,LoadAnimate
},
data() {
  return {
    hotMuisc:[],//200
    shownum:true,
    page:1    
  }
},
created(){
this.getHotMusic();
},
mounted(){
   window.addEventListener('scroll',this.windowScroll);
},
methods: {
  windowScroll(){
     let scrollTop=document.documentElement.scrollTop||document.body.scrollTop;
     let loadMoreoffsetTop=this.$refs.loadmore.offsetTop;
     let clientHeight=window.innerHeight;
    //  console.log(scrollTop,loadMoreoffsetTop,clientHeight);
     if(scrollTop+clientHeight>=loadMoreoffsetTop+33){
       this.page++;
     }
   },
  async getHotMusic(){
  await this.axios.get("/playlist/detail?id=3778678").then(re=>{
    // 
             this.hotMuisc=re.playlist.tracks.map((item)=>{
               item.song={
                 alias:item.alia,
                //  privilege:re.privilege[index],
                 artists:item.ar,
                 album:item.al
               }
               return item
             })
            //  console.log(this.hotMuisc);
  })
  },
},
computed: {
    showList(){
     let end=this.page*20;
       return this.hotMuisc.slice(0,end)
    }
},
 beforeDestroy(){
   window.removeEventListener('scroll',this.windowScroll);
 },
}
</script>

<style lang="scss" scoped>
.hotmuisc{
  .hotpic{
    position: relative;
    padding-top: 38.9%;
    overflow: hidden;
    background: url(@/assets/hot_music_bg_2x.jpg) no-repeat;
    background-size: contain;
    z-index: 1;
    .hotpic-title{
      width: 142px;
      height: 67px;
      background-image: url(@/assets/index_icon_2x.png);
      background-position: -24px -30px;
      background-size: 166px 97px;
      position: absolute;
      top: 50%;
      transform: translateY(-70%);
      left: 20px;
      z-index: 9;
    }
    .hot-time{
      display: inline-block;
      color: hsla(0,0%,100%,.8);
      font-size: 12px;
      position: absolute;
      top: 72%;
      left: 20px;
    }
 }
 .loadmore{
   text-align: center;
   font-size: 14px;
   line-height: 40px;
 }
}
 
</style>